<template>	
	<div id="pay-management">	
		<similarHeader title='支付管理' @goBack='goBack' :havaMore='false'></similarHeader>
		<ul class='list'>
			<li class='borderBottom'>
				实名认证
				<span class='right'>已认证</span>
			</li>
			<li style='border-bottom:1px solid #ededed;'>
				修改支付密码
			</li>
			<li class='borderBottom'>
				忘记支付密码
			</li>
			<li class='padding borderBottom'>
				指纹支付<br>
				<span class='fingerprint'>开启后，支付时可通过验证指纹，快速完成付款</span>
				<span class='right'>已开启</span>
			</li>
			<li style='border-bottom:1px solid #ededed;'>
				自动扣费
			</li>
			<li  class='borderBottom'>
				转账到账时间
				<span class='right'>实时到账</span>
			</li>
			<li style='border-bottom:1px solid #ededed;'>
				服务管理
			</li>
			<li>
				注销微信支付
			</li>
		</ul>
	</div>
</template>
<script>	
	import  similarHeader from './similar-header'
	export default{
		data(){
			return{

			}
		},
		methods:{
			goBack(){
				this.$emit('isShowPop',false)
			}
		},
		components:{
			similarHeader
		}

	}
</script>
<style scoped>
	#pay-management{
		background: #ededed;
		height: 100vh;
		overflow: hidden;
	}
	header{
		overflow: hidden;
		position: fixed;
		width: 100%;
		z-index: 666;
		top: 0;

		display: flex;
		align-items: center;
		position: relative;
	}
	header h2{
		font-size: .4rem;
		margin: .16rem;
		margin-left: .3rem;
		font-weight: 600;
	}
	header .goBack{
		margin-left: .2rem;
		width: .44rem;
		height: .44rem;
	}
	.list{
		background: #fff;
	}
	.list li {
		font-size: .38rem;
	    padding: .3rem 0;
	    position: relative;
	    margin-left: .3rem;
	    display: flex;
	    align-items: center;
	    background: #fff url('~img/images/right.png') no-repeat 96% center/.3rem;
	}
	.list li.borderBottom{
		border-bottom: .2rem solid #ededed;
		margin-left: 0;
		padding-left: .3rem;
		position: relative;
	}
	.list li .right {
		color:#b0b0b0;
		position: absolute;
		right: .8rem;
		top:50%;
		transform: translateY(-55%);
	}
	li .fingerprint{
		position: absolute;
		bottom: .1rem;
		color:#b0b0b0;
		font-size: .32rem;
	}
	.list li.padding{
		/*padding-bottom: .7rem;*/
		height: 8vh;
		padding-left: .3rem;
	}
</style>


