<template>
	<header class="similar">
		<!-- <slot name='demo'></slot> -->
		<!-- <slot name='goBack'></slot> -->
		<!-- <h2><slot name='title'></slot></h2> -->
		<!-- <slot name='more'></slot> -->
		<img src="~img/images/chat/left.png" alt="" class='goBack' @click='goBack'>
		<h2 v-show='title'>{{title}}</h2>
		<img src="~img/images/chat/more.png" alt="" class='more'  @click='goMore' v-show='havaMore'>
		<slot></slot>
	</header>
</template>
<script>
	export default{
		name:'similar',
		props:['title','havaMore'],
		methods:{
			goBack(){
				this.$emit('goBack');
			},
			goMore(){
				this.$emit('goMore');
			}
		}
	}
</script>
<style>
	
</style>


