<template>
	<header>
		<h3>{{title}}
			<div>
				<span class='search' @click.stop="goSearch"></span>
				<span class='add' @click.stop="headerAdd"></span>
			</div>		
		</h3>
		<ul class='layer' v-show='isShow'>
			<li>
				<img src="~img/images/header/info.png" alt="">发起群聊
			</li>
			<li>
				<img src="~img/images/header/adduser.png" alt="">添加朋友
			</li>
			<li>
				<img src="~img/images/header/saoyisao.png" alt="">扫一扫
			</li>
			<li>
				<img src="~img/images/header/shoukuan.png" alt="">收付款
			</li>
			<li>
				<img src="~img/images/header/email.png" alt="">帮助与反馈
			</li>
		</ul>	
	</header>
</template>
<script>
	export default{
		name:'we-header',
		props:['title','menuIndex'],
		data(){
			return{
				isShow:false,
			}
		},
		methods:{
			headerAdd(){
				if(this.menuIndex!==null && this.$route.name==='home'){
					this.$emit('childByValue',null)
				}
				else{
					this.isShow=!this.isShow;
				}
				
			},
			goSearch(){
				if(this.menuIndex){
					this.$emit('childByValue',null)
				}
				else{
					this.$router.push('/search');
				}
				
			},
		}
	}
</script>
<style scoped>
	header{
		background: #eee;
		border-bottom:1px solid rgb(211,211,211);
		position: fixed;
		width: 100%;
		z-index:666;
		top:0;
		overflow: inherit;
	}
	h3{
		font-size: .4rem;
		margin:.16rem;
		margin-left: .3rem;
		font-weight: 600;
	}
	header>h3>div{
		position: absolute;
	    margin-top: .07rem;
	    bottom: .1rem;
	    right: .16rem;
	    z-index:9999;
	}
	.search,.add{
		display: inline-block;
		height: .45rem;
		width: .45rem;
	}
	.search{
		background: url('~img/images/header/search.png') no-repeat center/cover;
		margin-right: .53rem;
	}
	.add{
		background: url('~img/images/header/add.png') no-repeat center/cover;
		margin-right: .2rem;
	}
	.layer{
		width: 3.3rem;
		background: #4c4c4c;
		position: absolute;
		right: .3rem;
		top:1rem;
		border-radius: 6px;
		z-index: 9999;
	}
	.layer:before{
		content: '';
		display: block;
		width: 0;
	    height: 0;
	    border-left: .1rem solid transparent;
	    border-right: .1rem solid transparent;
	    border-bottom: .2rem solid #4c4c4c;
	    position: absolute;
	    top:-.19rem;
	    right:.2rem;
	}
	li{
		font-size: .38rem;
		color: #fff;
		margin:.52rem .35rem;
		display: flex;
		align-items: center;
	}
	.layer li img{
		width: .5rem;
		height: .5rem;
		margin-right: .16rem;
		
	}
</style>


